import moment from 'moment'

export const pointIcon = 'image://data:image/svg+xml;base64,'

export const tooltipFormatter = (params) => {
  const { value, date } = params[0].data
  const formattedDate = moment(date).format('DD MMM YYYY')

  const styleTooltip = `
    display: flex;
    flex-direction: column;
    width: 150px;
    height: 90px;
    padding: 10px 0px 0px 10px; 
    background: white; 
    box-shadow: 0px 8px 32px rgba(18, 16, 37, 0.03);
    border-radius: 8px; 
    border: 1px solid rgba(18, 16, 37, 0.05);
    color: rgba(18, 16, 37, 0.5);
    font-family: 'Rubik', sans-serif;
    font-size: 11px;
    line-height: 16px;
  `
  const styleItem = `
    margin-bottom: auto;
  `
  const styleTitle = `
    text-transform: uppercase;
    font-weight: 500;
  `

  const styleCost = `
    font-size: 20px;
    color: #121025;
  `

  const styleData = `
    color: #A19FB0;
    font-size: 13px;
  `

  return `
    <div style="${styleTooltip}">
      <div style="${styleItem + styleTitle}">
        Total sold
      </div>
      <div style="${styleItem + styleCost}">
        ${value.toLocaleString()} €
      </div>
      <div style="${styleItem + styleData}">
        ${formattedDate}
      </div> 
    </div>
  `
}

export const labelFormatter = (item, index) => (index ? moment(item).format('DD') : '')
